import { clsx, type ClassValue } from "clsx";
import { twMerge } from "tailwind-merge";

/**
 * Merge Tailwind classes with clsx
 */
export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export function debounce<T extends (...args: unknown[]) => unknown>(
  func: T,
  wait: number
): (...args: Parameters<T>) => void {
  let timeout: ReturnType<typeof setTimeout> | null = null;
  return (...args: Parameters<T>) => {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => func(...args), wait);
  };
}

/**
 * Format cents to dollar string
 */
export function formatCents(cents: number): string {
  return new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  }).format(cents / 100);
}

/**
 * Convert dollars to cents
 */
export function dollarsToCents(dollars: number): number {
  return Math.round(dollars * 100);
}

/**
 * Convert cents to dollars
 */
export function centsToDollars(cents: number): number {
  return cents / 100;
}

/**
 * Format units (30-min increments) to hours string
 */
export function formatUnitsToHours(units: number): string {
  const hours = units / 2;
  if (hours === 1) return "1 hour";
  if (hours < 1) return `${units * 30} minutes`;
  return `${hours} hours`;
}

/**
 * Default fee constants (in basis points)
 */
export const DEFAULT_PLATFORM_FEE_BPS = 800; // 8%
export const DEFAULT_COMMUNITY_FEE_BPS = 500; // 5%
export const MAX_TOTAL_FEE_BPS = 9000; // 90% - max allowed combined fees

/**
 * Calculate fee from basis points
 */
export function calculateFeeFromBps(amount: number, bps: number): number {
  return Math.round(amount * (bps / 10000));
}

/**
 * Calculate platform fee (8% default)
 */
export function calculatePlatformFee(amount: number, bps: number = DEFAULT_PLATFORM_FEE_BPS): number {
  return calculateFeeFromBps(amount, bps);
}

/**
 * Calculate community fee in basis points
 */
export function calculateCommunityFee(amount: number, bps: number): number {
  return calculateFeeFromBps(amount, bps);
}

/**
 * Format basis points as percentage string
 */
export function formatBpsAsPercent(bps: number): string {
  return `${(bps / 100).toFixed(bps % 100 === 0 ? 0 : 1)}%`;
}

/**
 * Generate initials from name
 */
export function getInitials(name: string | null | undefined): string {
  if (!name) return "?";
  return name
    .split(" ")
    .map((n) => n[0])
    .join("")
    .toUpperCase()
    .slice(0, 2);
}

/**
 * Format date for display
 */
export function formatDate(date: Date | string): string {
  return new Intl.DateTimeFormat("en-US", {
    month: "short",
    day: "numeric",
    year: "numeric",
  }).format(new Date(date));
}

/**
 * Format date with time
 */
export function formatDateTime(date: Date | string): string {
  return new Intl.DateTimeFormat("en-US", {
    month: "short",
    day: "numeric",
    year: "numeric",
    hour: "numeric",
    minute: "2-digit",
  }).format(new Date(date));
}

/**
 * Format relative time (e.g., "2 hours ago")
 */
export function formatRelativeTime(date: Date | string): string {
  const now = new Date();
  const then = new Date(date);
  const diffMs = now.getTime() - then.getTime();
  const diffSecs = Math.floor(diffMs / 1000);
  const diffMins = Math.floor(diffSecs / 60);
  const diffHours = Math.floor(diffMins / 60);
  const diffDays = Math.floor(diffHours / 24);

  if (diffSecs < 60) return "just now";
  if (diffMins < 60) return `${diffMins}m ago`;
  if (diffHours < 24) return `${diffHours}h ago`;
  if (diffDays < 7) return `${diffDays}d ago`;
  return formatDate(date);
}
